export default {
  load: function ($data, widgetContext, viewModels) {
    this.widgetContext = widgetContext;
    this.$data = $data;

    this.instantiateViewModels(viewModels);
  },

  beforeAppear: function (pageContext) {
    this.pageContext = pageContext;
    $.Topic('widget.' + this.$data.id() + '.beforeAppear').publish();
  },

  instantiateViewModels: function (viewModels) {
    let instance = this;

    Object.keys(viewModels).forEach((viewModelName) => {
      let viewModel = viewModels[viewModelName];
      let dataMethods = viewModel.prototype.$dataMethods;

      viewModel.widgetContext = this.widgetContext;

      $.extend(this.$data, viewModel.prototype.$data || {});
      viewModel.prototype.$data = this.$data;
            
      viewModel.prototype.applyContexts = function (context) {
        instance.widgetContext.viewModels[viewModelName] = context;
      };

      if(dataMethods) {
        Object.keys(dataMethods).forEach(function (methodKey) {
          viewModel.prototype.$data[methodKey] = function () {
            return dataMethods[methodKey].apply(this.viewModels[viewModelName], arguments);
          }
        });
        delete viewModel.prototype.$dataMethods;
      }
      
      let newInstance = new viewModel();
      this.widgetContext.viewModels[viewModelName] = newInstance;
      
      $.Topic('widget.' + this.$data.id() + '.beforeAppear').subscribe(() => {
        let currentViewModel = this.widgetContext.viewModels[viewModelName];

        if(typeof currentViewModel.beforeAppear === 'function') {
          currentViewModel.beforeAppear(this.pageContext);
        }
      });
    });
  }
};



