import { widget as widgetLoader } from './loaders';

export function init(allViewModels) {  
  return {
    viewModels: {},

    onLoad($data) {
      widgetLoader.load($data, this, allViewModels);
    },

    beforeAppear(pageContext) {
      widgetLoader.beforeAppear(pageContext);
    }
  };
};



